input {
  border: none;
}

button:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: #66FCF1;
}

::-webkit-input-placeholder .input-line:focus +::input-placeholder {
  color: #45A29E;
}

.highlight {
  color: rgba(141, 67, 67, 0.247);
  font-weight: 400;
  cursor: pointer;
  -webkit-transition: color .2s ease;
  transition: color .2s ease;
}

.highlight:hover {
  color: rgb(241, 240, 163);
  -webkit-transition: color .2s ease;
  transition: color .2s ease;
}

.spacing {
  flex-grow: 1;
  height: 120px;
  font-weight: 300;
  text-align: center;
  margin-top: 10px;
  color: rgba(255, 255, 255, 0.65)
}

.input-line:focus {
  outline: none;
  border-color: rgb(223, 125, 125);
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.ghost-round {
  cursor: pointer;
  background: none;
  border: 1px solid rgba(121, 206, 240, 0.65);
  border-radius: 25px;
  color: #66FCF1;
  align-self: flex-end;
  font-size: 15px;
  font-size: 0.8rem;
  font-family: roboto;
  font-weight: 400;
  line-height: 2.5em;
  margin-top: 10px;
  /* margin-bottom: 10px; */
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.ghost-round:hover {
  background: rgb(110, 247, 194);
  color: rgb(247, 78, 78);
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.input-line {
  background: none;
  margin-bottom: 10px;
  margin-left: 20px;
  margin-right: 20px;
  line-height: 2.4em;
  color: #fff;
  font-family: roboto;
  font-weight: 300;
  letter-spacing: 0px;
  letter-spacing: 0.02rem;
  font-size: 19px;
  font-size: 1.2rem;
  border-bottom: 1px solid rgb(32, 255, 99);
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

